body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,body,#root {
	height: 100%;
	width: 100%;
}

* {
	font-size: 100%;
	font-family: "Skia";
}

:root {
  --main-color: #E8E8E8;
  --font-color: #96939B;
  --btn-font-color: white;
  --primary-btn-color: #75BCC6;
  --error-color: #ed6173;
  --accent-color: #86E0B5;
}

.App {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 100%;
}

.TutorialPage {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 100%;
}

.LogoPanel {
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	height: 20%;
	width: 100%;
}

.TitleImage {
	height: 80%;
}

.GeneratePage {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	height: 90%;
	width: 100%;
}

.PatternDisplay {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 80%;
	width: 70%;
	background-color: white;
}

.TutorialPanel {
	display: flex;
	flex-direction: row;
	align-items: center;
	background-color: #E8E8E8;
	background-color: var(--main-color);
	color: #96939B;
	color: var(--font-color);
	font-size: .8vw;
	text-align: center;
	height: 70%;
	width: 60%;
	padding: 5%;
}

.DescriptionPanel {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	height: 100%;
	width: 50%;
}

.CuteButton {
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #75BCC6;
	background-color: var(--primary-btn-color);
	cursor: pointer;
	color: white;
	color: var(--btn-font-color);
	border-radius: 5px;
}

.BackBtn, .NextBtn, .FinishBtn {
	font-size: 1.5vw;
	height: 100%;
	width: 9%;
	border-radius: 10%;
	margin: 5px;
}

.FinishBtn {
	background-color: #86E0B5;
	background-color: var(--accent-color);
}

.PrintBtn {
	font-size: 1.5vw;
	height: 100%;
	width: 80%;
}

.MeasurementImage {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 50%;
}

.DescriptionTitle {
	display: flex;
	height: 10%;
	width: 100%;
	font-size: 3.4vw;
}
.DescriptionText {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 70%;
	width: 100%;
	font-size: 1.5vw;
	white-space: pre-wrap;
}

.InputPanel {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 10%;
	width: 100%;
}

.ErrorPanel {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 5%;
	width: 100%;
	font-size: 1.2vw;
	color: #ed6173;
	color: var(--error-color);
}

.MeasurementInput {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 30%;
	font-size: 1.8vw;
	border:none;
	border-radius: 5px;
}

